html {
    height: 100%;
  }

body {
  position: relative;
  margin: 0;
  min-height: 100%;
  font-family: Slack-Larsseit,"Helvetica Neue",Helvetica,"Segoe UI",Tahoma,Arial,sans-serif;
}

#footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 1rem;
    text-align: center;
    border-top: 1px solid gray;
}

#footer-item-container {
    margin: auto;
    padding: 5px;
    width: 300px;
    height: 30px;
}

.footer-item {
    margin: 20px;
}

.footer-image:hover {
    opacity: 0.8;
    cursor: pointer;
}
#menubar-container {
    width: 100%;
    padding: 20px 0px 20px 0px;
    text-align: right;
    transition: 0.5s;
    z-index: 2;
    background-color: #263238;
}

#menubar-item-list {
    display: flex;
}

.menubar-item {
    font-size: 20px;
    padding: 10px;
    margin: 50px;
    color: #E0E0E0;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    opacity: 0.7;
    transition: 0.3s;
    border-bottom: 1px solid transparent;
}

.menubar-item:hover {
    opacity: 1;
    border-bottom: 1px solid #E0E0E0;
}

#mobile-menu-button {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 4px;
    transition: 0.3s;
    margin: 10px;
}

#mobile-menu-button:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

#mobile-menu-button:focus {
    outline: none;
}

#mobile-menubar-container {
    width: 100%;
    display: none;
    text-align: right;
    background-color: #263238;
}

#mobile-menubar-item-container {
    display: inline-block;
    transition: 0.3s;
    width: 100%;
    background-color: lightgray;
}

#mobile-menubar-item-list {
    list-style: none; 
    padding-left: 0px;
    text-align: left; 
    margin-top: 25px;
}

#mobile-menubar-item {
    font-size: 20px;
    padding: 5px;
    color: black;
    font-family: Varela Round, sans-serif;
    text-decoration: none;
    transition: 0.4s;
    opacity: 0.9;
    border-radius: 5px;
    width: 100%;
}

#mobile-menubar-item:hover {
    opacity: 1;
}

#mobile-menubar-list-item {
    padding: 20px;
    transition: 0.3s;
}

#mobile-menubar-list-item:hover {
    background-color: gray;
    transition: 0.3s;
    cursor: pointer;
}

.mobile-menu-line {
    width: 35px;
    height: 3px;
    background-color: black;
    margin: 6px 0;
    border-radius: 20px;
}

@media screen and (max-width: 830px) {
    #menubar-container {
        display: none;
    }

    #mobile-menubar-container {
        display: block;
    }
}
#home-body-container {
    width: 100%;
    background-color: #263238;
}

#home-body-container div {
    width: 80%;
    margin: auto;
}

#home-body-container h1 {
    text-align: left;
    font-size: 80px;
    margin-bottom: 0px;
    margin-top: 0px;
    width: 50%;
    color: #E0E0E0;
}

#home-body-container h3 {
    text-align: left;
    font-size: 40px;
    margin-top: 0px;
    color: #E0E0E0;
}

#home-body-container p {
    text-align: left;
    font-size: 20px;
    width: 50%;
    color: #E0E0E0;
    margin-bottom: 0px;
}

#home-top-image {
    height: 650px;
    width: 500px;
    min-width: 300px;
    min-height: 450px;
}

#top-content {
    font-size: 14px;
}

#icon-list {
    list-style: none; 
    display: flex; 
    margin: auto; 
    padding: 5px;
}

#home-top-title-container {
    margin: 200px;
}

#home-top-title {
    text-align: left;
}

#home-middle-section {
    font-family: Varela Round, sans-serif;
}

#home-middle-section img {
    width: 200px;
    height: 200px;
}

#middle-section-container {
    display: flex;
    padding: 20px 150px 20px 300px;
}

#about-image {
    max-width: 350px;
    margin: 20px;
}

#middle-content {
    max-width: 600px;
    margin: 120px;
}

#project-section {
    font-family: Varela Round, sans-serif;
    background-color: #263238;
    height: 1000px;
}

#skills-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex-grow: 1;
    justify-content: center;
}

#skills-container div {
    margin: 60px;
    transition: 0.3s;
}

#skills-container p {
    font-size: 25px;
}

#skills-container p + p {
    font-size: 15px;
    color: gray;
}

#contact-section {
    background-color: #78909C;
    padding: 50px;
}

#contact-section a {
    color: black;
    padding: 10px;
    transition: 0.3s;
    text-decoration: none;
    font-size: 17px;
    border-radius: 5px;
    background-color: gray;
    box-shadow: 0px 4px 27px -10px rgba(0,0,0,0.4);
}

#contact-section a:hover {
    cursor: pointer;
    background-color: hsl(0, 0%, 62%);
}

#contact-section p:first-child {
    font-size: 40px;
    margin-bottom: 0px;
}

#contact-section p:first-child + p {
    font-size: 30px;
    margin-top: 10px;
}

footer {
    bottom: 0; 
    width: 100%; 
    border-top: 1px solid lightgray; 
    height: 100px; 
    background-color: #E2E2E2;
}

footer div {
    font-size: 13px;
    margin: auto;
}

footer a {
    text-decoration: none;
    opacity: 0.8;
}

footer a:hover {
    opacity: 1;
}

footer img {
    width: 30px;
    height: 30px;
}

footer ul {
    width: 200px;
    margin: auto;
}

footer li {
    margin: 15px;
}

@media screen and (max-width: 1190px) {
    #home-body-content {
        display: block;
        width: 100%
    }

    #home-top-title-container {
        margin: 10px;
    }

    #home-top-title {
        text-align: center;
    }

    #home-top-image {
        width: 80%;
    }

    #top-content {
        margin: auto;
    }

    #icon-list {
        width: 300px;
    }
}

@media screen and (max-width: 660px) {
    .project-items {
        height: 700px;
    }

    #project-section {
        height: 1500px;
    }

    #skills-container img {
        width: 200px;
    }

    #skills-container div {
        margin: 10px;
    }
}
#project-container {
    width: 100%;
    font-family: Varela Round, sans-serif;
}

#project-item-container {
    width: 400px;
    height: 400px;
    background-color: rgb(97, 97, 97);
    padding: 5px;
    margin: 5px;
    border-radius: 50px;
    box-shadow: 0px 4px 7px -4px rgba(0,0,0,0.75);
    transition: 0.3s;
}

#project-item-container:hover {
    cursor: pointer;
    margin-top: 0px;
    box-shadow: 0px 4px 27px -10px rgba(0,0,0,0.75);
    transition: 0.3s;
}

#project-item-button {
    background-color: transparent;
    border: 1px solid black;
    border-radius: 4px;
    transition: 0.3s;
}

#project-item-button:hover {
    background-color: lightgray;
    transition: 0.3s;
    cursor: pointer;
}

#project-item-button:focus {
    outline: none;
}

#project-item-link {
    text-decoration: none;
    color: #474646;
}

#project-button-container {
    height: 30px;
    padding-top: 50px;
    padding-right: 70px;
}

.project-item-image {
    width: 100%;
    height: 100%;
}

.project-item-desc {
    font-size: 16px;
    margin-bottom: 5px;
    position: absolute;
    overflow-wrap: break-word;
    position: relative;
    color: white;
}

.project-items {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    justify-content: space-evenly;
}

@media screen and (max-width: 1160px) {
    .items {
        display: block;
        width: 500px;
    }

    #project-item-container {
        width: 300px;
        height: 300px;
    }

    .project-item-image {
        height: 60%;
    }

    #project-container {
        margin-bottom: 50px;
        height: 3300px;
    }
}

@media screen and (max-width: 600px) {
    .items {
        display: block;
        width: 300px;
    }
}

#about-container {
    width: 500px;
    margin: auto auto 50px auto;
    font-family: Varela Round, sans-serif;
}

#about-header {
    text-align: center;
}

#about-text {
    font-size: 25px;
    text-align: left;
}

@media screen and (max-width: 610px) {
    #about-container {
        width: 400px;
    }
}

@media screen and (max-width: 480px) {
    #about-container {
        width: 300px;
    }
}

@media screen and (max-width: 410px) {
    #about-container {
        width: 250px;
        font-size: 14px;
    }

    #about-text {
        font-size: 14px; 
    }
}
.skill-item-figure {
    padding-top: 50px; 
    max-width: 250px;
    min-width: 250px;
}

.skill-item-image {
    max-height: 100px; 
    min-height: 50px;
}

.skill-item-figure-caption {
    text-align: center; 
    font-size: 18px; 
    margin-top: 50px;
}

.skills-list-container {
    text-align: left;
}

.skills-list {
    list-style: none;
}

.skills-container img {
    min-width: 200px;
    min-height: 200px;
}

@media screen and (max-width: 1050px) {

    .skill-item-container {
        display: block;
    }

    .skill-item-figure {
        padding-top: 0px; 
        max-width: 0px;
    }

    .skills-list {
        font-size: 12px;
        padding-left: 0px;
    }

    .skills-list-container {
        text-align: center;
    }
}

@media screen and (min-width: 1050px) {
    .skill-item-figure {
        margin: auto;
    }
}

@media screen and (max-width: 870px) {
    #skills-container {
        width: 600px;
    }
}

@media screen and (max-width: 650px) {
    .skill-item-container {
        margin-left: 100px;
        width: 400px;
    }
}

@media screen and (max-width: 530px) {
    .skill-item-container {
        margin-left: 50px;
        width: 400px;
    }
}

@media screen and (max-width: 470px) {
    .skill-item-figure {
        margin-left: 20px;
    }

    .skill-item-container {
        margin-left: 50px;
        width: 300px;
    }
}

@media screen and (max-width: 370px) {
    .skill-item-figure {
        margin-left: 0px;
    }

    .skill-item-container {
        margin-left: 40px;
        width: 250px;
    }

    .skill-item-image {
        height: 75px;
    }

    #skills-container {
        width: 100%;
    }
}
body {
    text-align: center;
}
